<template>
    <section>
        <div class="import">
            <div class="form-item">
                <label>用户类型:
                    <select class="select">
                        <optgroup label="国籍">
                            <option value="0">中国</option>
                            <option value="1">外籍</option>
                        </optgroup>
                    </select>
                </label>
            </div>
            <div class="form-item">
                <label>文件:
                    <input type="file" name="excel" class="excel"
                           accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"/>
                    <div class="blank"></div>
                    <input class="submit" type="button" value="导入" @click="check()">
                </label>
            </div>
        </div>
    </section>
</template>

<script>

    import axios from 'axios';
    import loginCheck from '@/components/util/loginCheck';

    export default {
        data() {
            return {
                interval: '',
                amount: 0
            }
        },
        methods: {
            choose(e){

            },
            check() {
                var self = this;
                loginCheck().then(function () {
                    let file = $('.excel').get(0).files[0];
                    let typeMap = ['chinaUser', 'abroadUser'];
                    let type = typeMap[$('.select').val()];
                    let cid = Math.round(Math.random() * 10000000000);
                    if (!file) {
                        alert('请先选择文件');
                    } else {
                        let config = {
                            headers: {'Content-Type': 'multipart/form-data'}
                        };
                        let formData = new window.FormData();
                        formData.append('file', file);
                        formData.append('type', type);
                        formData.append('cid', cid);
                        self.$emit('showOverLay', true);
                        self.upload('admin/import/upload', formData, config);
                        self.interval = setInterval(()=> {
                            self.checkProcess('admin/counter/checkCounter', cid);
                        }, 1000);
                    }
                })
            },
            checkProcess(pathname, cid) {
                let self = this;
                axios.post(pathname, {cid})
                        .then(response => {
                            if (response.data.errno === 0) {
                                self.$emit('setProcess', response.data.total);
                            } else {

                            }
                        })
                        .catch(err => {
                            console.log(err);
                        })
            },
            upload(pathName, data, config) {
                let self = this;
                axios.post(pathName, data, config)
                        .then(response => {
                            if (response.data.errno === 101) {
                                alert(response.data.msg);
                                this.$router.push({name: 'login'});
                            } else if (response.data.errno === 0) {
                                clearInterval(this.interval);
                                self.$emit('hideOverLay', 'done', response.data.msg || '操作完成', response.data.successAmount, response.data.failAmount);
                            } else {
                                clearInterval(this.interval);
                                self.$emit('hideOverLay', 'fail', response.data.msg || '数据格式有误');
                            }
                        })
                        .catch(err => {
                            clearInterval(this.interval);
                            self.$emit('hideOverLay', 'fail', '系统异常');
                            console.log('来自错误定位：' + err);
                        })
            }
        }
    }
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped rel="stylesheet/less">
    .import {
        font-size: 18px;
        font-weight: 400;
        .form-item {
            margin-top: 10px;
        }
        .select {
            padding: 0 10px;
            border: 1px solid rgba(102, 102, 102, 0.3);
        }
        .submit {
            cursor: pointer;
            margin-top: 20px;
            border-radius: 3px;
            font-size: 20px;
            color: #fff;
            background-color: rgba(25, 139, 21, 0.8);
            padding: 5px 10px;
            &:hover {
                background-color: forestgreen;
            }
        }
    }
</style>


