<template>
    <section class="side-menu">
        <div>
            <ul @click="choose($event)">
                <li data-name="deal">
                    数据处理
                </li>
                <li data-name="import">
                    数据导入
                </li>
                <li data-name="search">
                    数据查询
                </li>
            </ul>
        </div>
    </section>
</template>

<script>
    export default {
        data() {
            return {}
        },
        methods: {
            choose(e){
                let selected = $(e.target).data("name");
                this.$emit('selectChange',selected);
            }
        }
    }
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped rel="stylesheet/less">
    .side-menu {
        float: left;
        border-radius: 5px;
        border: 1px solid rgba(102, 102, 102, 0.3);
        background-color: #fff;
        margin-right: 20px;
        display: inline-block;
        li {
            font-size: 24px;
            cursor: pointer;
            padding: 10px;
            border-bottom: 1px solid rgba(102, 102, 102, 0.3);
            &:last-child {
                border-bottom: none;
            }
            &:hover {
                color: #fff;
                background-color: #5480cc;
            }
        }
    }
</style>


