<template>
    <section>
        <div class="search">
            <div class="form-item">
                <label>数据状态:
                    <select v-model.number="userStatus" class="select data-status">
                        <optgroup label="状态">
                            <option value="1">成功</option>
                            <option value="0">失败</option>
                        </optgroup>
                    </select>
                </label>
            </div>
            <div class="form-item">
                <label>用户类型:
                    <select v-model.number="isAbroad" class="select is-abroad">
                        <optgroup label="国籍">
                            <option value="0">中国</option>
                            <option value="1">外籍</option>
                        </optgroup>
                    </select>
                </label>
            </div>
            <div class="form-item" v-model="reasonType" v-show="!userStatus">
                <label>失败原因:
                    <select v-model="reasonType" class="select fail-reasonType">
                        <optgroup label="出错原因">
                            <option value="all">全部</option>
                            <option value="ERR001">数据项存在重复</option>
                            <option value="ERR002">关键数据项为空</option>
                            <option value="ERR003">身份证格式有误</option>
                            <option value="ERR004">编码格式有误</option>
                            <option value="ERR005">电话号码格式有误</option>
                            <option value="ERR006">推荐人电话号码格式有误</option>
                            <option value="ERR007">邮箱格式有误</option>
                            <option value="ERR008">群架构下没有会员</option>
                            <option value="ERR009">编码与职位信息不符</option>
                        </optgroup>
                    </select>
                </label>
            </div>
            <div class="form-item">
                <label>大区号:
                    <input v-model.trim="groupNum" class="small" type="text">
                </label>
            </div>
            <div class="form-item">
                <label>用户编码:
                    <input v-model.trim="code" type="text">
                </label>
            </div>
            <div class="form-item">
                <input type="button" class="submit query" v-model="queryText" :disabled="isQuerying" @click="submit(1)">
            </div>
            <div class="form-item">
                <input type="button" v-show="tbody.length" v-model="exportText" class="submit export"
                       :disabled="isExporting" @click="exportData()">
            </div>
            <div class="form-item">
                <span class="notice" v-text="noticeText"></span>
            </div>
            <div class="total" v-show="totalNum !== 0">查询总数: {{totalNum}}</div>
            <div class="table-data" v-show="tbody.length">
                <table>
                    <thead>
                    <tr>
                        <th>群号</th>
                        <th>姓名</th>
                        <th>性别</th>
                        <th>职务</th>
                        <th>编码</th>
                        <th>导入时间</th>
                        <th>身份证号</th>
                        <th>电话号码</th>
                        <th>电子邮箱</th>
                        <th>地区</th>
                        <th>推荐人</th>
                        <th>推荐人电话</th>
                        <th>备注</th>
                        <th>错误原因</th>
                        <th>操作</th>
                    </tr>
                    </thead>
                    <tbody class="tbody">
                    <tr v-for="(value, index) in tbody">
                        <td v-for="(ivalue, iindex) in value">{{ivalue}}</td>
                        <td><input type="button" class="submit delete" value="删除" @click="deleteRow($event,value[4])">
                        </td>
                    </tr>
                    </tbody>
                </table>
                <div class="pagination-wrapper">
                    <div class="pre-page" v-show="pageNow > 1" @click="submit(pageNow - 1)">上一页</div>
                    <ul class="pagination" v-show="pageNow <= pageTotal">
                        <li v-show="index <= pageLimit" v-for="index in pageTotal" @click="submit(index)">{{index}}</li>
                    </ul>
                    <div class="next-page" v-show="pageNow < pageTotal" @click="submit(pageNow + 1)">下一页</div>
                    <div class="page-counter" v-show="pageTotal">共 {{pageTotal}} 页</div>
                    <div class="page-jumper" v-show="pageTotal > pageLimit">
                        第<input v-model.trim="jumpNum" class="small" type="text">页
                        <input type="button" class="submit query" value="跳转" @click="jump()">
                        <span class="jump-notice" v-text="jumpNoticeText"></span>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>

    import axios from 'axios';
    import loginCheck from '@/components/util/loginCheck';

    const JUMP_NUM_REG = /^[1-9]|([1-9][0-9]+)$/;
    const P_NUMBER_REG = /^\d+$/;
    const CODE_REG = /^[A-Z]{9,10}(\d)+$/;

    export default {
        data() {
            return {
                userStatus: 1,
                isAbroad: 0,
                reasonType: 'all',
                groupNum: '',
                code: '',
                noticeText: '',
                tbody: [],
                totalNum: 0,
                pageTotal: 0,
                pageLimit: 10, // 显示的最大页数
                rowLimit: 10, // 每页显示的最大行数
                jumpNum: 1, // 跳转初始页码
                pageNow: 1,
                jumpNoticeText: '',
                searchPathname: 'admin/useroperation/searchUser',
                exportPathname: 'admin/export/exportUser',
                deletePathname: 'admin/useroperation/deleteUser',
                exportGroup: {},
                isExporting: false,
                isQuerying: false,
                queryText: '查询',
                exportText: '导出'
            }
        },
        methods: {
            verifyData(beforeRes) {
                let self = this;
                let flag = true;

                if (self.groupNum && !P_NUMBER_REG.test(self.groupNum)) {
                    self.noticeText = '大区号格式有误';
                    flag = false;
                }

                // 编码可以为空，如果不为空就检查
                if (self.code && !CODE_REG.test(self.code)) {
                    self.noticeText = '编码格式有误';
                    flag = false;
                }

                if (flag) {
                    return beforeRes;
                } else {
                    Promise.reject(self.noticeText);
                }
            },
            query(beforeRes) {
                let start = beforeRes.start;
                let userStatus = this.userStatus;
                let isAbroad = this.isAbroad;
                let reasonType = this.reasonType;
                let groupNum = this.groupNum;
                let code = this.code;
                let len = this.rowLimit; // 这里是行数

                let param = {userStatus, isAbroad, start, len};

                if (groupNum) {
                    groupNum = parseInt(groupNum).toString();
                    if (groupNum.length == 1) {
                        groupNum = '0' + groupNum;
                    }
                    param.groupNum = groupNum;
                }
                if (code) {
                    param.code = code;
                }
                if (!userStatus && reasonType !== 'all') {
                    param.reasonType = reasonType;
                }
                this.exportGroup = $.extend(true, {}, param);
                delete this.exportGroup.start;
                delete this.exportGroup.len;

                this.isQuerying = true;
                this.queryText = '查询中...';

                axios.post(beforeRes.pathname, param)
                        .then(response => {
                            this.isQuerying = false;
                            this.queryText = '查询';
                            if (response.data.errno === 0) {
                                let data = response.data.user;
                                this.pageNow = this.jumpNum = beforeRes.start;
                                if (data.length) {
                                    let docfrag = [];
                                    for (let key of Object.keys(data)) {
                                        let tr = [];
                                        let td = [];
                                        let item = data[key];
                                        for (let ikey of Object.keys(item)) {
                                            td = item[ikey];
                                            tr.push(td);
                                        }
                                        tr.push();
                                        docfrag.push(tr);
                                    }
                                    this.tbody = docfrag;
                                    let total = response.data.total;
                                    this.totalNum = total;
                                    if (total % this.rowLimit === 0) {
                                        this.pageTotal = total / this.rowLimit
                                    } else {
                                        this.pageTotal = Math.floor(total / this.rowLimit) + 1
                                    }
                                } else {
                                    this.noticeText = '没有符合的数据';
                                }
                            } else {

                            }
                        }).catch(err => {
                    this.isQuerying = false;
                    this.queryText = '查询';
                    this.noticeText = '系统内部错误,查询失败';
                    return Promise.reject(err);
                })
            },
            submit(start) {
                if (this.isQuerying || this.isExporting) {
                    alert('请等候结果');
                    return;
                }
                var self = this;
                this.tbody = [];
                this.pageTotal = 0;
                this.noticeText = '';
                let param = {pathname: this.searchPathname, start};
                loginCheck({thirdParam: param}).then(this.verifyData).then(this.query).catch(err => {
                    console.log(err);
                });
            },
            deleteRow(event, code) {
                let targetTr = event.target.parentNode.parentNode;
                let pathname = this.deletePathname;
                let param = {code, pathname};
                loginCheck({thirdParam: param}).then(this.deleteQuery).then(() => {
                    targetTr.parentNode.removeChild(targetTr);
                }).catch(err => {
                    console.log(err);
                });
            },
            deleteQuery(beforeRes) {
                let code = beforeRes.code;
                let isAbroad = this.isAbroad;
                let userStatus = this.userStatus;
                let param = {code, isAbroad, userStatus};
                axios.post(beforeRes.pathname, param)
                        .then(response => {
                            if (response.data.errno === 0) {
                                alert('删除成功');
                                return true;
                            } else {
                                alert('删除失败');
                                Promise.reject('删除失败');
                            }
                        }).catch(err => {
                    alert('删除失败');
                    return Promise.reject(err);
                })
            },
            jump() {
                this.jumpNoticeText = '';
                let jumpNum = parseInt(this.jumpNum);
                if (JUMP_NUM_REG.test(jumpNum) && jumpNum <= this.pageTotal) {
                    this.submit(jumpNum);
                } else {
                    this.jumpNoticeText = '请输入合法页码'
                }
            },
            exportData () {
                let param = {pathname: this.exportPathname, exportGroup: this.exportGroup};
                loginCheck({thirdParam: param}).then(this.exportQuery).catch(err => {
                    console.log(err);
                });
            },
            exportQuery(beforeRes) {
                this.isExporting = true;
                this.isQuerying = true;
                this.exportText = '导出中...';
                let config = {
                    headers: {'Accept': 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'}
                };
                axios.post(beforeRes.pathname, beforeRes.exportGroup, config)
                        .then(response => {
                            this.isExporting = false;
                            this.isQuerying = false;
                            this.exportText = '导出';
                            if (response.data.errno === 0) {
                                location.href = location.href.replace(/\/#\/index/, '') + '/exports/' + response.data.filename;
                            } else {

                            }
                        }).catch(err => {
                    this.isExporting = false;
                    this.isQuerying = false;
                    this.exportText = '导出';
                    this.noticeText = '系统错误，无法导出';
                    return Promise.reject(err);
                })
            }
        }
    }
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped rel="stylesheet/less">
    .search {
        font-size: 18px;
        font-weight: 400;
        .form-item {
            input[type='text'] {
                &.small {
                    width: 60px;
                }
                padding-left: 20px;
                border-radius: 3px;
                border: 1px solid rgba(102, 102, 102, 0.3);
            }
            &:not(:last-child) {
                margin-right: 20px;
            }
            display: inline-block;
            margin-top: 10px;
            .notice {
                color: brown;
            }
        }
        .total {
            font-weight: 400;
            margin-top: 10px;
        }
        .table-data {
            margin-top: 20px;
            table {
                border: 1px solid rgba(102, 102, 102, 0.3);
                border-collapse: collapse;
                tr {
                    th {
                        font-weight: 400;
                        padding: 10px;
                        border: 1px solid rgba(102, 102, 102, 0.3);
                    }
                    td {
                        font-size: 12px;
                        padding: 10px;
                        border: 1px solid rgba(102, 102, 102, 0.3);
                    }

                }
            }
        }
        .pagination-wrapper {
            margin-top: 20px;
            .pre-page, .next-page {
                cursor: pointer;
                display: inline-block;
                text-align: center;
                padding: 5px;
                border: 1px solid rgba(102, 102, 102, 0.3);
            }
            .pagination {
                display: inline-block;
                li {
                    cursor: pointer;
                    display: inline-block;
                    width: 30px;
                    text-align: center;
                    padding: 5px 0;
                    border: 1px solid rgba(102, 102, 102, 0.3);
                    &:hover {
                        color: #FFF;
                        background-color: rgba(25, 139, 21, 0.8);
                    }
                }
            }
            .page-counter {
                display: inline-block;
            }
            .page-jumper {
                margin-left: 20px;
                display: inline-block;
                input[type='text'] {
                    &.small {
                        width: 50px;
                    }
                    padding-left: 20px;
                    border-radius: 3px;
                    border: 1px solid rgba(102, 102, 102, 0.3);
                }
                &:not(:last-child) {
                    margin-right: 20px;
                }
            }
            .jump-notice {
                color: brown;
            }
        }
        .select {
            padding: 0 10px;
            border: 1px solid rgba(102, 102, 102, 0.3);
        }
        .submit {
            cursor: pointer;
            border-radius: 3px;
            font-size: 20px;
            color: #fff;
            padding: 5px 10px;
            &.query {
                width: 120px;
                background-color: rgba(25, 139, 21, 0.8);
                &:hover {
                    background-color: forestgreen;
                }
                &[disabled='disabled'] {
                    opacity: 0.6;
                    background-color: rgba(25, 139, 21, 0.8);
                }
            }
            &.export {
                width: 120px;
                background-color: rgba(0, 66, 139, 0.8);
                &:hover {
                    background-color: #00448b;
                }
                &[disabled='disabled'] {
                    opacity: 0.6;
                    background-color: rgba(0, 66, 139, 0.8);
                }
            }
            &.delete {
                width: 60px;
                background-color: darkred;
                font-size: 16px;
                padding: 3px 10px;
                &:hover {
                    background-color: brown;
                }
            }
        }
    }
</style>


