<template>
    <section class="page-index" v-if="showPage">
        <div class="overlay" :class="{
        'enable':isEnable,
        'visible':isVisible
        }">
            <div class="notice" v-show="noticeStatus === 'dealing'">
                <img src="./img/dealing.gif">
                <span class="text" v-text="noticeText"></span>
            </div>

            <div class="dialog" v-show="noticeStatus === 'done' || noticeStatus === 'fail'">
                <div class="success-dialog" v-show="noticeStatus === 'done'">
                    <span class="title">导入成功</span>
                    <span class="total" v-text="total"></span>
                    <span class="success-total" v-text="successTotal"></span>
                    <span class="fail-total" v-text="failTotal"></span>
                    <input type="button" class="submit done-submit" value="确定" @click="hide()">
                </div>
                <div class="fail-dialog" v-show="noticeStatus === 'fail'">
                    <span class="title">导入失败</span>
                    <span class="fail-text" v-text="noticeText"></span>
                    <input type="button" class="submit fail-submit" value="确定" @click="hide()">
                </div>
            </div>
        </div>
        <header-nav></header-nav>
        <div class="main">
            <side-menu
                    @selectChange="selectChange"
            >
            </side-menu>
            <div class="container">
                <div class="auto" v-if="selected === 'auto'">
                    <div class="title">欢迎使用CDC后台管理系统</div>
                    <div class="content">请选择左侧操作</div>
                </div>
                <div class="none" v-if="selected === 'none'">
                    <div class="title">功能尚未开通</div>
                    <div class="content">敬请期待</div>
                </div>
                <import v-if="selected === 'import'"
                        @setProcess="setProcess"
                        @showOverLay="showOverLay"
                        @hideOverLay="hideOverLay"
                ></import>
                <Search v-if="selected === 'search'"
                        @setProcess="setProcess"
                        @showOverLay="showOverLay"
                        @hideOverLay="hideOverLay"
                ></Search>
            </div>
        </div>
        <dev-footer></dev-footer>
    </section>
</template>

<script>

    import axios from 'axios'
    import loginCheck from '@/components/util/loginCheck'
    import headerNav from '@/components/pages/header/header'
    import sideMenu from '@/components/pages/side-menu/side-menu'
    import Import from '@/components/pages/index/import/import'
    import Search from '@/components/pages/index/search/search'
    import devFooter from '@/components/pages/footer/footer'


    export default {
        name: 'index',
        data () {
            return {
                selected: 'auto',
                showPage: true,
                isEnable: false,
                isVisible: false,
                noticeStatus: '',
                noticeText: '',
                total: '',
                successTotal: '',
                failTotal: ''
            }
        },
        computed: {},
        methods: {
            selectChange(selected) {
                // 未处理单元
                let noneArr = ['deal'];
                noneArr.forEach(function (value, index) {
                    if (selected === value) {
                        selected = 'none';
                    }
                });
                this.selected = selected;
            },
            setProcess(value) {
                this.noticeText = '导入条数: ' + value;
            },
            showOverLay() {
                this.noticeText = '导入条数: 0';
                this.noticeStatus = 'dealing';
                this.isVisible = true;
                this.isEnable = true;
            },
            hideOverLay(type, msg, successAmount, failAmount) {

                this.noticeStatus = type;
                this.noticeText = msg;
                if (successAmount || failAmount) {
                    this.total = '导入总数： ' + (successAmount + failAmount);
                    this.successTotal = '合规条数: ' + successAmount;
                    this.failTotal = '不合规条数: ' + failAmount;
                }
            },
            hide() {
                let self = this;
                self.isVisible = true;
                self.isEnable = false;
                setTimeout(()=> {
                    self.isVisible = false;
                    this.total = 0;
                    this.successTotal = 0;
                    this.failTotal = 0;
                }, 2000);
            }
        },
        components: {
            headerNav,
            sideMenu,
            Import,
            Search,
            devFooter
        },
        created() {

        },
        mounted() {

        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped rel="stylesheet/less">
    .page-index {
        width: 100vw;
        height: 100vh;
        position: relative;
        .overlay {
            visibility: hidden;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, .6);
            position: absolute;
            z-index: 100;
            opacity: 0;
            transition: opacity 2s cubic-bezier(.23, 1, .32, 1) 0s;
            &.enable {
                opacity: 1;
            }
            &.visible {
                visibility: visible;
            }
            .notice {
                text-align: center;
                display: inline-block;
                padding: 10px 20px;
                line-height: 80px;
                border-radius: 10px;
                position: absolute;
                top: 50%;
                left: 50%;
                background-color: #000;
                opacity: 0.7;
                transform: translate(-50%, -50%);
                img {
                    vertical-align: middle;
                    width: 60px;
                    height: 60px;
                }
                .text {
                    margin-left: 10px;
                    color: #FFF;
                    font-size: 24px;
                    vertical-align: middle;
                }
            }
            .dialog {
                overflow: hidden;
                text-align: center;
                padding: 10px 20px;
                border-radius: 10px;
                position: absolute;
                top: 50%;
                left: 50%;
                background-color: #FFF;
                transform: translate(-50%, -50%);
                span {
                    margin-top: 20px;
                    display: inline-block;
                    width: 100%;
                    &.title {
                        margin-top: 10px;
                        font-size: 24px;
                        color: #111;
                    }
                    &.total {
                        font-size: 18px;
                    }
                    &.success-total {
                        font-size: 18px;
                        color: forestgreen;
                    }
                    &.fail-total {
                        font-size: 18px;
                        color: brown;
                    }
                    &.fail-text {
                        font-size: 18px;
                        color: brown;
                    }
                }
                .submit {
                    cursor: pointer;
                    display: inline-block;
                    margin-top: 20px;
                    border-radius: 3px;
                    font-size: 20px;
                    color: #fff;
                    background-color: rgba(25, 139, 21, 0.8);
                    padding: 5px 30px;
                    &:hover {
                        cursor: pointer;
                        background-color: forestgreen;
                    }
                }
            }
        }
        .main {
            min-height: 660px;
            padding: 80px 20px 20px 20px;
            .container {
                border-radius: 5px;
                border: 1px solid rgba(102, 102, 102, 0.3);
                background-color: #fff;
                padding: 20px;
                overflow: hidden;
                .title {
                    font-weight: 400;
                    font-size: 24px;
                    color: #111111;
                }
                .content {
                    margin-top: 20px;
                    font-size: 16px;
                    color: #222222;
                }
                .auto {

                }
            }
        }
    }
</style>



